@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");

* {
    font-family: "Roboto", sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

div.login-div {
    margin: 20px auto;
    padding: 30px;
    text-align: center;
    background: #e8e8e8;
    width: 95%;
    max-width: 400px;
    border-radius: 12px;

    input {
        padding: 5px;
        margin: 10px;
        border-radius: 4px;
        border: none;

        %:focus {
            outline: none;
            border: 1px solid #18aeff;
         }
    }
}

